<template>
  <v-flex>
    <span v-if="!noLabel" class="text-label">{{ label || "&nbsp;" }}</span>
    <v-text-field
      outlined
      :name="name"
      :placeholder="placeholder"
      :append-outer-icon="checked ? 'mdi-check' : ''"
      :type="!password ? 'text' : 'password'"
      :messages="comment"
      v-model="value"
    ></v-text-field>
  </v-flex>
</template>

<script>
export default {
  name: "STextInput",
  props: {
    noLabel: Boolean,
    label: String,
    name: String,
    placeholder: String,
    comment: String,
    checked: Boolean,
    password: Boolean,
  },
  data() {
    return {
      value: "",
    };
  },
  watch: {
    value(val) {
      this.$emit("input", val);
    },
  },
};
</script>

<style lang="sass" scope>
.text-label
  font-weight: 700
  color: black

.v-input
  margin-top: 5px !important
</style>
