<template>
  <v-container fluid class="sampleform-container" justify="center">
    <div v-for="(field, index) in fields" v-bind:key="index">
      <s-text-input
        v-if="field.type == 'text'"
        :placeholder="field.placeholder"
        :label="field.label"
        v-model="formValues[field.hash]"
      />
      <s-select-box
        v-else-if="field.type == 'select'"
        :placeholder="field.placeholder"
        :label="field.label"
        v-model="formValues[field.hash]"
        :items="
          field.options.map((o) => ({
            text: o.label,
            value: o.value,
          }))
        "
      />
    </div>
    <v-btn depressed color="primary" @click="save()"> SAVE </v-btn>
  </v-container>
</template>

<script>
import SSelectBox from "@/components/symmetry/SSelectBox";
import STextInput from "@/components/symmetry/STextInput";

export default {
  name: "SampleForm",
  components: {
    SSelectBox,
    STextInput,
  },
  data() {
    return {
      label: "",
      fields: [],
      formValues: {},
    };
  },
  computed: {},
  created() {
    this.$admin.http
      .post("/api/viewform", {
        hash: "ed060f1e99abdd7b87982d302d08f2b4dcb7f21335f30a8bf551baf03f2699c95694b8521c5c2a935a5c06139a120a4046e6db3021a5de4115ba64a6c9964676",
      })
      .then((res) => {
        if (res && res.data) {
          this.label = res.data.label;
          this.fields = res.data.fields;
          this.formValues = res.data.fields.reduce(
            (obj, f) => ({ ...obj, [f.hash]: "" }),
            {}
          );
        }
      })
      .catch((err) => console.error(err));
  },
  watch: {},
  methods: {
    save() {
      this.$admin.http
        .post("/api/saveform", {
          hash: "ed060f1e99abdd7b87982d302d08f2b4dcb7f21335f30a8bf551baf03f2699c95694b8521c5c2a935a5c06139a120a4046e6db3021a5de4115ba64a6c9964676",
          fields: this.formValues,
        })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => console.error(err));
    },
  },
};
</script>

